module.exports = [{"ImageName":"L-NH10-A4A1-005.jpg","EntityName":"Takin","EntityDescription":"A takin (Budorcas taxicolor) is a large, yellowish-brown animal. It often appears to be smiling because of the shape of its mouth.","AltText":"Select the name that identifies a large, yellowish-brown animal, with hooves and impressive horns. It often appears to be smiling and is sometimes called a goat-antelope."},{"ImageName":"takin 2.jpg","EntityName":"Takin","EntityDescription":"A takin (Budorcas taxicolor) is a large, yellowish-brown animal sometimes called a \"goat-antelope.\"","AltText":"Select the name that identifies a large, yellowish-brown animal, with hooves and impressive horns. It often appears to be smiling and is sometimes called a goat-antelope."},{"ImageName":"takin in camera trap.jpg","EntityName":"Takin","EntityDescription":"A takin (Budorcas taxicolor) is a large, yellowish-brown animal that lives in the mountains of China, India, Bhutan, and Myanmar.","AltText":"Select the name that identifies a large, yellowish-brown animal, with hooves and impressive horns. It often appears to be smiling and is sometimes called a goat-antelope."},{"ImageName":"takinnight.jpg","EntityName":"Takin","EntityDescription":"A takin (Budorcas taxicolor) is a large, yellowish-brown animal with hooves and impressive horns. ","AltText":"Select the name that identifies a large, yellowish-brown animal, with hooves and impressive horns. It often appears to be smiling and is sometimes called a goat-antelope."},{"ImageName":"M35_2 reference from Bill.jpg","EntityName":"Yellow-throated marten","EntityDescription":"The yellow-throated marten (Martes flavigula) is included under the China Wildlife Protection Law.","AltText":"Select the name that identifies a small black and yellow animal with a long fluffy tail. Related to otters and weasels, it tends to stay close to the ground, scurrying along logs or behind rocks and tree stumps."},{"ImageName":"yellowthroatmarten1.jpg","EntityName":"Yellow-throated marten","EntityDescription":"The yellow-throated marten (Martes flavigula) can be found across Asia. They are solitary animals; adults usually meet only to breed.","AltText":"Select the name that identifies a small black and yellow animal with a long fluffy tail. Related to otters and weasels, it tends to stay close to the ground, scurrying along logs or behind rocks and tree stumps."},{"ImageName":"yellowthroatmarten2.jpg","EntityName":"Yellow-throated marten","EntityDescription":"The yellow-throated marten (Martes flavigula) tends to stay close to the ground, scurrying along logs or behind rocks and tree stumps.","AltText":"Select the name that identifies a small black and yellow animal with a long fluffy tail. Related to otters and weasels, it tends to stay close to the ground, scurrying along logs or behind rocks and tree stumps."},{"ImageName":"yellowthroatmarten3.jpg","EntityName":"Yellow-throated marten","EntityDescription":"The yellow-throated marten (Martes flavigula) is a small carnivorous mammal related to weasels, wolverines, and otters. Visit the Zoo's otters at Asia Trail and American Trail.","AltText":"Select the name that identifies a small black and yellow animal with a long fluffy tail. Related to otters and weasels, it tends to stay close to the ground, scurrying along logs or behind rocks and tree stumps."},{"ImageName":"asiaticblackbear1.jpg","EntityName":"Asiatic black bear","EntityDescription":"Asiatic black bears (Ursus thibetanus) are larger than giant pandas and only have a small amount of white fur. The white patch on their chest led to their nickname, the moon bear.","AltText":"The animal in this photograph is very large and is almost entirely black, except for a small patch of white fur on its chest. It has big claws and is in the same family as giant pandas."},{"ImageName":"asiaticblackbear2.jpg","EntityName":"Asiatic black bear","EntityDescription":"Asiatic black bears (Ursus thibetanus) are an endangered species. They have long been hunted and are also threatened by habitat loss.","AltText":"The animal in this photograph is very large and is almost entirely black, except for a small patch of white fur on its chest. It has big claws and is in the same family as giant pandas."},{"ImageName":"L-HTL12-A07A-088.jpg","EntityName":"Chinese serow","EntityDescription":"The Chinese serow (Capricornis milneedwardsii) shares parts of its range with the giant panda. It is often photographed by camera trap in mountain forests.","AltText":"Select the name that identifies a medium-size, greyish-brown animal with goat-like horns and a horse-like mane."},{"ImageName":"L-HTL12-A07A-121.jpg","EntityName":"Chinese serow","EntityDescription":"The Chinese serow (Capricornis milneedwardsii) is solitary, and mostly nocturnal. Camera trap photos have snapped images of the serow at night and during the day, however.","AltText":"Select the name that identifies a medium-size, greyish-brown animal with goat-like horns and a horse-like mane."},{"ImageName":"d30640s98i3.jpg","EntityName":"Chinese serow","EntityDescription":"With its goat-like horns, horse-like mane, and ears like a donkey, the Chinese serow (Capricornis milneedwardsii) might appear unusual to observers from countries outside of Asia.","AltText":"Select the name that identifies a medium-size, greyish-brown animal with goat-like horns and a horse-like mane."},{"ImageName":"d30657s12i1.jpg","EntityName":"Chinese serow","EntityDescription":"The Chinese serow (Capricornis milneedwardsii) is often photographed by camera trap in mountain forests. It tends to eat grasses, leaves, and shoots from plants easily reached from beaten paths.","AltText":"Select the name that identifies a medium-size, greyish-brown animal with goat-like horns and a horse-like mane."},{"ImageName":"golden pheasant.jpg","EntityName":"Golden pheasant","EntityDescription":"An adult male golden pheasant (Chrysolophus pictus) can be more than three feet (one meter) long. Its tail can be twice as long as its body!","AltText":"The feathered, two-legged animal shown in this camera trap photo sports an array of colors, long tail feathers, and a brightly colored head. Can you name it?"},{"ImageName":"goldenpheasant2.jpg","EntityName":"Golden pheasant","EntityDescription":"As in many bird species, the male golden pheasant (Chrysolophus pictus) is much more colorful than the female. Males are also larger than females. We chose to use camera trap photos of the more distinctive and more noticeable male. ","AltText":"The feathered, two-legged animal shown in this camera trap photo sports an array of colors, long tail feathers, and a brightly colored head. Can you name it?"},{"ImageName":"L-NH11-D7A1-016.jpg","EntityName":"Golden pheasant","EntityDescription":"Golden pheasants (Chrysolophus pictus) nest in trees at night, but spend much of the day on the ground, often hidden in the tall grasses. They eat berries, leaves, seeds, and insects.","AltText":"The feathered, two-legged animal nestled in the bushes in this camera trap photo sports an array of colors, long tail feathers, and a brightly colored head. Can you name it?"},{"ImageName":"goldpheasant1.jpg","EntityName":"Golden pheasant","EntityDescription":"In the late 1980s, the golden pheasant (Chrysolophus pictus) was considered \"near threatened.\" In 2016, it was evaluated as \"least concern.\" That time period coincides with much stricter protection measures of giant pandas and their habitat. ","AltText":"The feathered, two-legged animal shown in this camera trap photo sports an array of colors, long tail feathers, and a brightly colored head. Can you name it?"},{"ImageName":"L-NH11-F2B1-004.jpg","EntityName":"Wild boar","EntityDescription":"Wild boars (Sus scrofa) are found on every contintent except Antarctica. They are in the same family (Suidae) as Red River Hogs (Potamochoerus porcus), an African wild pig. Visit the Zoo's Red River Hogs at Cheetah Conservation Station near the Visitor Center.","AltText":"Select the name for the bristly black animal moving through the bamboo in this camera trap photo. It has coarse hair, a wide snout, beedy eyes, and white patches along its head that could be tusks. "},{"ImageName":"giantpanda2.jpg","EntityName":"Giant panda","EntityDescription":"It would be hard to mistake this animal. Giant pandas (Ailuropoda melanoleuca) have distinctive black-and-white fur and are talented tree climbers.","AltText":"In this nighttime camera trap photo, a large black-and-white animal seems to be climbing down from a tree--or perhaps scratching its rump on the bark. Can you \"bear\" to guess its name?"},{"ImageName":"giantpanda4.jpg","EntityName":"Giant panda","EntityDescription":"Conservation scientists use camera trap photos of giant pandas (Ailuropoda melanoleuca) and other animals to help determine where to focus some of their bamboo habitat restoration efforts. If pandas are spotted using a particular corridor, we want to be sure they will always have a good food supply nearby.  ","AltText":"In this photo, a large black-and-white animal moves down a path and pauses to sniff at a tree. Turn around, bear--the bamboo is behind you! "},{"ImageName":"giantpanda5.jpg","EntityName":"Giant panda","EntityDescription":"Like all bears, giant pandas (Ailuropoda melanoleuca) are omnivores. But they are unique because they must eat bamboo. It's rare for an animal to absolutely need one food for survival, especially one they don't even digest very well. That's one reason why it is so important to study panda habitats. ","AltText":"Peek-a-boo! This photo only caught the head of a large black-and-white animal, peering out from some bamboo, its favorite food. This should be an easy one to name!"},{"ImageName":"L-NH11-D7A1-019.jpg","EntityName":"Golden snub-nosed monkey","EntityDescription":"As this picture indicates, golden snub-nosed monkeys (Rhinopithecus roxellana) don't mind cold and snow. In fact, this species is only found in mountain forests where it frequently snows. It is threatened by habitat loss, and is likely to decline further due to climate change. ","AltText":"Sitting in a small clearing by a patch of snow, this primate shows off all of its distinctive characteristics: yellow-gold coloring, a darker ridge of fur on the crest of its head, and, especially, a pale, flat snout. "},{"ImageName":"goldensnubnose2.jpg","EntityName":"Golden snub-nosed monkey","EntityDescription":"Many primates live in social groups. Bands of golden snub nosed monkeys (Rhinopithecus roxellana) can range in size from small, with fewer than ten individuals, to very large with hundreds of monkeys. Visit some of the Zoo's primates at Gibbon Ridge, Lemur Island, the Small Mammal House, Think Tank, and the Great Ape House.","AltText":"Like many primates, the animal in this camera trap photo can walk on all fours. This one curls its long tail and points its stubby white nose and lion-like ruff straight at the camera. Maybe it can spot the box hidden in the brush."},{"ImageName":"goldensnubnose5.jpg","EntityName":"Golden snub-nosed monkey","EntityDescription":"Even moving away from the camera trap so that its distinctive face isn't visible, the long tail and golden fur give an indication of which primate this is. If you clicked on golden snub-nosed monkey (Rhinopithecus roxellana), you are correct! ","AltText":"Even moving away from the camera trap so that its distinctive face isn't visible, the long tail and golden fur give an indication of which primate this is. Even this description kind of gives it away!"},{"ImageName":"blueeared1.jpg","EntityName":"Blue-eared pheasant","EntityDescription":"We added this camera trap photo of a blue-eared pheasant (Crossoptilon auritum) to see if you're paying attention. It looks very different from the golden pheasant with whom it shares its habitat!","AltText":"We haven't talked about this creature before. It has red legs and talons and light-colored feathers that stand up like ears on either side of its head. Its body is completely grey except for black and white tail feathers."},{"ImageName":"L-NH11-A2B1-016.jpg","EntityName":"Tufted deer","EntityDescription":"Tufted deer (Elaphodus cephalophus) are an unprotected species, whose population is likely decreasing. Perhaps it will be helped by giant panda and bamboo habitat conservation efforts. ","AltText":"Perched on a leafy hillside, this medium sized creature is a greyish brown color. It has four legs, a short tail folded close to a white rump, and mouse-like ears that seem too big for its head. A patch of dark fur on the top of its head gives it its name."},{"ImageName":"leopardcat2.jpg","EntityName":"Leopard cat","EntityDescription":"The population of Leopard cats (Prionailurus bengalensis) is declining, mainly due to loss of habitat. Restoring bamboo forests saves these animals, too.","AltText":"Like the much larger predator with whom it shares its name, this animal has dark marks, commonly spots or rosettes. Even in a black-and-white camera trap photo, its feline shape and distinctive markings help identify it. Can you guess what we are describing?"},{"ImageName":"leopardcat3.jpg","EntityName":"Leopard cat","EntityDescription":"Leopard cats (Prionailurus bengalensis) are carnivores and like giant pandas and yellow-throated martens are solitary animals that only meet other adults to breed.","AltText":"Like the much larger predator with whom it shares its name, this animal has dark marks, commonly spots or rosettes. It is very similar in size and shape to a common household pet, but make no mistake--it is definitely a wild feline!"},{"ImageName":"L-NH11-E8A1-024.jpg","EntityName":"Malayan porcupine","EntityDescription":"This Malayan porcupine (Hystrix brachyura) looks similar to its North American cousins, which you can visit here at the Zoo.","AltText":"A camera trap snapped a photo of this spiky little creature moving through the undergrowth at night. It doesn't need to worry too much about predators with all of those sharp needles covering its back! Can you guess what it is?"}]

